<template>
  <div>
		<div class="option-content">
			<ul>
				<li v-for="(name, course) in lessons">
					<a
						v-bind:href="'#'"
						v-on:click="toggle(course)"
					>{{ name }}</a>
				</li>
			</ul>
		</div>
		<prj class="body-content" v-if="lesson === 'project'"></prj>
		<game class="body-content" v-else-if="lesson === 'game'"></game>
		<w3d class="body-content" v-else-if="lesson === '3w3d'"></w3d>
		<graph class="body-content" v-else-if="lesson === 'graphics'"></graph>
	</div>
</template>

<script>
import prj from './project/prj.vue';
import game from './gamedesign/game.vue';
import w3d from './3W3D/3w3d.vue';
import graph from './graphics/graph.vue';

export default {
  name: 'lesson',
	data() {
		return {
			lesson: 'project',
			lessons: {
				'project'  : '畢業專題',
				'game'     : '遊戲程式設計',
				'3w3d'     : '3W3D',
				'graphics' : '計算機圖學'
			}
		}
	},
	methods: {
		toggle: function(course) {
			this.lesson = course;
		}
	},
	components: {
		prj,
		game,
		w3d,
		graph
	}
}
</script>

<style lang="scss">
.body-content {
	width: 1000px;
	height: 100%;
}
.option-content {
	display: flex;
	justify-content: center;
	width: 200px;
	height: 100%;
	padding: 30px;
	a {
		font-family: 'Microsoft JhengHei';
		font-weight: bold;
	}
}
</style>



