<template>
  <div>
		<modal></modal>
		<div id="carouselExampleIndicators" class="carousel slide carousel-fade" data-ride="carousel">
			<ol class="carousel-indicators">
				<li data-target="#carouselExampleIndicators" data-slide-to="0" class="active"></li>
				<li
					data-target="#carouselExampleIndicators"
					v-for="n in 3"
					v-bind="{ 'data-slide-to': n }"
				></li>
			</ol>
			<div class="body-content carousel-inner">
				<div class="carousel-item active">
					<a
						v-bind:href="'#'"
						v-bind="{ 'data-toggle': 'modal', 'data-target': '#exampleModalCenter'}"
						v-on:click="myVideo()">
						<img
							class="image-size d-block w-100"
							v-bind:src="start"
						>
					</a>
					<div class="p-hw-link carousel-caption d-none d-md-block">
						<h5>初始畫面</h5>
						<a
							target="_blank"
							v-bind:href="'https://psychoticvenom.github.io/project/index.html'"
						>Play Game</a>
					</div>	
				</div>
				<div
					class="carousel-item"
					v-for="(image, imgName) in prjImgs">
					<a
						v-bind:href="'#'"
						v-bind="{ 'data-toggle': 'modal', 'data-target': '#exampleModalCenter'}">
						<img
							class="image-size d-block w-100"
							v-bind:src="image"
						>
					</a>
					<div class="p-hw-link carousel-caption d-none d-md-block">
						<h5>{{ imgName }}</h5>
						<a
							v-bind="{ 'target': '_blank' }"
							v-bind:href="'https://psychoticvenom.github.io/project/index.html'"
						>Play Game</a>
					</div>	
				</div>
			</div>
			<a class="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
				<span class="carousel-control-prev-icon" aria-hidden="true"></span>
				<span class="sr-only">Previous</span>
			</a>
			<a class="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
				<span class="carousel-control-next-icon" aria-hidden="true"></span>
				<span class="sr-only">Next</span>
			</a>
		</div>
	</div>
</template>

<script>
import win from './img/win.jpg';
import dead from './img/dead.jpg';
import start from './img/start.jpg';
import running from './img/running.jpg';
import modal from './components/modal.vue';

export default {
  name: 'project',
	data() {
		return {
			start: start,
			prjImgs: {
				'遊戲畫面' : running,
				'勝利畫面' : win,
				'死亡畫面' : dead
			}
		}
	},
	methods: {
		myVideo: function() {

		}
	},
	components: {
		modal
	}
}
</script>

<style lang="scss">
.image-size {
	width: 1000px;
	height: 550px;
}
.essay-frame {
	float: left;
	width: 65%;
	height: 100%;
	background-color: aquamarine;
	span {
		font-family: 'Microsoft JhengHei';
		font-size: 40px;
	}
}
.option-frame{
	float: left;
	width: 35%;
	height: 100%;
	background-color: blueviolet;
}
.p-hw-link{
	h5{
		font-family: 'Microsoft JhengHei';
		font-weight: bold;
		color: black;
		text-shadow: 1px 1px 2px #ffffff;
	}
	a{
		font-weight: bold;
		color: aqua;
		text-shadow: 1.5px 1.5px 2px #000000;
		&:hover {
			color: red;
		}
	}
}
</style>



