<template>
  <div id="App">
    <div class="head-frame">
      <div class="head-content">
        <span>{{ name }}</span>
        <div class="btn-frame">
          <ul>
            <li v-for="(pageName, pagePronoun) in buttons" class="btn">
              <a 
                v-bind:href="'#'"
                v-on:click="change(pagePronoun)"
              >{{ pageName }}</a>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <homePage class="body-frame" v-if="pages === 'home'"></homePage>
    <lessonPage class="body-frame" v-else-if="pages === 'univ'"></lessonPage>
  </div>
</template>

<script>
import homePage from './components/home.vue';
import lessonPage from './components/university/univ.vue';

export default {
  name: 'pages',
  data () {
    return {
      pages: 'univ',
      name: '黃暐程的部落格',
      buttons: {
        //'home'    : '首頁',
        //'aboutMe' : '關於我',
        'univ'    : '在學作品',
        //'extra'   : '課外讀物'
      }
    }
  },
  methods: {
    change: function(msg){
      this.pages = msg;
      console.log(this.pages);
    }
  },
  components: {
    homePage,
    lessonPage
  }
}
</script>

<style lang="scss">
body{
	margin: 0;
}
.head-frame{
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 150px;
  background-color: aqua;
}
.head-content{
	width: 1200px;
	height: 100%;
}
.head-content span{
	font-family: 'Microsoft JhengHei';
	font-size: 60px;
}
.btn-frame{
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 20px;
	width: 100%;
	height: 40px;
  ul{
    margin: 0;
    padding: 0;
    list-style: none;
  }
}
.btn{
  font-family: 'Microsoft JhengHei';
  font-weight: bold;
	display: inline-block;
	width: 125px;
	height: 100%;
}
.body-frame {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 550px;
}
</style>



