						<span aria-hidden="true">&times;</span>
					</button>
				</div>
				<div class="modal-body">
					<iframe class="video" src="https://www.youtube.com/embed/aZ_zFfShlxQ" frameborder="0" allow="accelerometer; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
				</div>
				<!-- modal-footer
				<div class="modal-footer">
					<button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
					<button type="button" class="btn btn-primary">Save changes</button>
				</div>
				-->
			</div>
		</div>
	</div>
</template>

<script>
export default {
  name: 'modal',
	data() {
		return {
			
		}
	}
}
</script>

<style lang="scss">
.modal-header {
	h5 {
		font-family: 'Microsoft JhengHei';
		font-weight: bold;
		font-size: 30px;
	}
}
.modal-body {
	height: 600px;
	.video {
		width: 100%;
		height: 100%;
	}
}
</style>



