<template>
  <div>
		<div id="carouselExampleIndicators" class="carousel slide carousel-fade" data-ride="carousel">
			<ol class="carousel-indicators">
				<li data-target="#carouselExampleIndicators" data-slide-to="0" class="active"></li>
				<li
					v-for="n in 3"
					v-bind="{ 'data-slide-to': n, 'data-target': '#carouselExampleIndicators' }"
				></li>
			</ol>
			<div class="body-content carousel-inner">
				<div class="carousel-item active">
					<img
						class="image-size d-block w-100"
						v-bind:src="HW1"
					>
					<div class="g-hw-link carousel-caption d-none d-md-block">
						<h5>My HW1</h5>
						<a
							v-bind="{ 'target': '_blank' }"
							v-bind:href="'https://psychoticvenom.github.io/CGHW/HW1.html'"
						>Press Me</a>
					</div>	
				</div>
				<div
					class="carousel-item"
					v-for="(HWgif, HWname, num) in homeworks">
					<img
						class="image-size d-block w-100"
						v-bind:src="HWgif"
					>
					<div class="g-hw-link carousel-caption d-none d-md-block">
						<h5>My {{ HWname }}</h5>
						<a
							v-bind="{ 'target': '_blank' }"
							v-bind:href="'https://psychoticvenom.github.io/CGHW/HW'+ (num + 3) +'.html'"
						>Press Me</a>
					</div>	
				</div>
			</div>
			<a class="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
				<span class="carousel-control-prev-icon" aria-hidden="true"></span>
				<span class="sr-only">Previous</span>
			</a>
			<a class="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
				<span class="carousel-control-next-icon" aria-hidden="true"></span>
				<span class="sr-only">Next</span>
			</a>
		</div>
	</div>
</template>

<script>
import HW1 from './img/graph1.gif';
import HW3 from './img/graph3.gif';
import HW4 from './img/graph4.gif';
import HW5 from './img/graph5.gif';

export default {
  name: 'graph',
	data() {
		return {
			HW1: HW1,
			homeworks: {
				'HW3' : HW3,
				'HW4' : HW4,
				'HW5' : HW5,
			}
		}
	}
}
</script>

<style lang="scss">
.image-size {
	width: 1000px;
	height: 550px;
}
.essay-frame {
	float: left;
	width: 65%;
	height: 100%;
	background-color: aquamarine;
	span {
		font-family: 'Microsoft JhengHei';
		font-size: 40px;
	}
}
.option-frame{
	float: left;
	width: 35%;
	height: 100%;
	background-color: blueviolet;
}
.g-hw-link{
	h5{
		color: yellow;
		text-shadow: 1px 1px 2px #000000;
	}
	a{
		font-weight: bold;
		color: aqua;
		text-shadow: 1.5px 1.5px 2px #000000;
		&:hover {
			color: red;
		}
	}
}
</style>



