<template>
  <div class="body-frame">
    <div class="body-content">
      <div class="essay-frame">
        <span>{{ essayTitle }}</span>
        <textarea></textarea>
      </div>
      <div class="option-frame"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'body',
  data () {
    return {
      essayTitle: 'I am title'
    }
  }
}
</script>

<style lang="scss">
.body-frame{
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	background-color: cornflowerblue;
}
.body-content{
	width: 1000px;
	height: 100%;
	background-color: brown;
}
.essay-frame{
	float: left;
	width: 65%;
	height: 100%;
	background-color: aquamarine;
}
.essay-frame span{
	font-family: 'Microsoft JhengHei';
	font-size: 40px;
}
.option-frame{
	float: left;
	width: 35%;
	height: 100%;
	background-color: blueviolet;
}
</style>



